.card {
  width: 100%;
  margin: 10px auto;
  border-radius: hidden;
  cursor: pointer;
  transition: 0.4s;
  border-color: black;
}

.card-img {
  width: 100%;
}

.card:hover {
  transform: scale(0.9, 0.9);
  box-shadow: 5px 5px 30px 15px rbga(0, 0, 0, 0.25),
    -5px -5px 30px 15px rbga(0, 0, 0, 0.22);
}
